@import url(https://fonts.googleapis.com/css?family=Lato:300,400,700);
@import url(https://fonts.googleapis.com/css?family=Anton);
body{
  margin: 0;
  padding: 0;
}

.badge {
  background: #ffffff;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.5);
  border-radius: 8px 8px 8px 8px;
  overflow: hidden;
  height: auto;
  width: 100%;
}

.badge_header {
  padding: 0.5rem 0;
  height: 80px;
  background: #1b1b25;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
}

.badge_section-name {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  padding: 1rem 0;
  font-size: 25px;
}

.badge_section-info {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  padding: 0.5rem 0;
  background: #f4f4f7;
}

.badge_avatar {
  border-radius: 50%;
  margin-right: 1rem;
  width: 120px;
  height: 120px;
}

.badge_footer {
  height: 54px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  color: #98ca3f;
  font-weight: bold;
  font-style: italic;
}

.BadgeNew__hero {
  width: 100%;
  padding: 2rem 1.2rem;
  background: url(/platzi-badges/static/media/estrellas.0abcdfcd.svg), #1B1B25;
  background-repeat: repeat;
  margin-bottom: 1rem;
  color: #ffffff;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
}

.form-control{
  margin-bottom: 20px;
}

.email{
  font-size: 12px;
}
.Badges__hero {
  width: 100%;
  padding: 2rem 0;
  background: url(/platzi-badges/static/media/estrellas.0abcdfcd.svg), #1B1B25;
  background-repeat: repeat;
  margin-bottom: 1rem;
}

.Badges__hero>.Badges__container {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
}

.Badges__container {
  width: 100%;
  max-width: 580px;
  margin: 0 auto;
  padding: 0 1rem;
}

li {
  margin: 0 auto;
  display: -webkit-flex;
  display: flex;
  max-width: 400px;
/*   border: 1px solid black;*/
  margin-bottom: 20px;
  border-radius: 10px;
  box-shadow: 0px 1px 6px 0px #1B1B25;
  padding: 20px;
  -webkit-justify-content: start;
          justify-content: start;
  -webkit-align-self: center;
          align-self: center;
}

li img {
  border-radius: 30px;
  height: 80px;
  width: 80px;
  -webkit-align-self: center;
          align-self: center;
  margin-right: 20px;
}

.userData {
  font-size: 12px;
}

.userData p{
  margin: 0;
}

.Badge_conf-logo {
  margin-bottom: 2rem;
}

.Badges__buttons {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
  margin-right: 8%;
  margin-bottom: 1rem;
}

.notFound{
  text-align: center;
}

.no{
  display: none;
}

@media (max-width: 700px) { 
  li{
    width: 300px;
  }
}
#loadP{
  width: 250px;
  margin: 0;
}

#loading{
  margin-top: 30px;
}

span{
  margin: 0;
  padding: 0;
}

#circle{
  -webkit-align-self: center;
          align-self: center;
  margin-right: 20px;
}

@media (max-width: 700px){
  #loadP{
    width: 150px;
  }
}


.errorMessage{
  display: block;
  text-align: center;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  height: 100vh;
}

.error{
  -webkit-align-items: center;
          align-items: center;

}

#errorButton{
  font-size: 30px;
  padding: 6px 56px;
}

#errorFont{
  font-size: 60px;
  font-weight: bolder;
  margin: 0;
}
.Navbar {
  width: 100%;
  padding: 0.5rem 0;
  background-color: #1C3643;
}

.Navbar__brand {
  color: #ffffff;

  display: -webkit-flex;

  display: flex;
  -webkit-align-items: center;
          align-items: center;
}

.Navbar__brand:hover {
  color: #ffffff;
  text-decoration: none;
}

.Navbar__brand-logo {
  margin-right: 0.5rem;
}
* {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

body {
  font-family: 'Lato', 'Helvetica Neue', sans-serif;
  background-color: #F4F4F7;
}

h1 {
  font-family: 'Anton', sans-serif;
}

a.link-unstyled {
  color: inherit;
}

a.link-unstyled:hover {
  color: inherit;
  text-decoration: none;
}

.btn {
  padding: 0.375rem 2rem;
}

.btn.btn-primary {
  color: #fff;
  background-color: #7DCD40;
  border-color: #7DCD40;
}

.btn.btn-primary:not(:disabled):not(.disabled):hover {
  color: #fff;
  background-color: #7DCD40;
  border-color: #7DCD40;
}

.btn.btn-primary:not(:disabled):not(.disabled):active {
  color: #fff;
  background-color: #7DCD40;
  border-color: #7DCD40;
}

.btn.btn-primary:not(:disabled):not(.disabled):focus {
  box-shadow: 0 0 0 0.2rem rgba(125, 205, 64, 0.5);
}

.btn.btn-danger {
  color: #fff;
  background-color: #CD4040;
  border-color: #CD4040;
}

.btn.btn-danger:not(:disabled):not(.disabled):hover {
  color: #fff;
  background-color: #CD4040;
  border-color: #CD4040;
}

.btn.btn-danger:not(:disabled):not(.disabled):active {
  color: #fff;
  background-color: #CD4040;
  border-color: #CD4040;
}

.btn.btn-danger:not(:disabled):not(.disabled):focus {
  box-shadow: 0 0 0 0.2rem rgba(255, 64, 64, 0.5);
}
